import axios from "axios";
import {installInterceptors} from "@jx3box/jx3box-common/js/axios";

const qs = require("qs");
const {__helperUrl} = require("@jx3box/jx3box-common/js/jx3box.json");
const $http = axios.create({
    withCredentials: true,
    baseURL: __helperUrl,
});
installInterceptors($http);

// ÁôæÁßëÊîªÁï•
let WikiPost = {
    // Ëé∑ÂèñÊúÄÊñ∞ÊîªÁï•
    newest(type, id, supply = 1) {
        if (!id) return;
        return $http({
            method: "GET",
            url: `/api/wiki/post`,
            headers: {Accept: "application/prs.helper.v2+json"},
            params: {type: type, source_id: id, supply: supply},
        })
    },

    // Ëé∑ÂèñÊúÄÊñ∞ÊîªÁï•ÂàóË°®
    newests(type) {
        return $http({
            method: "GET",
            url: `/api/wiki/posts/newest`,
            headers: {Accept: "application/prs.helper.v2+json"},
            params: {type: type},
        });
    },

    // Ëé∑ÂèñÂéÜÂè≤ÁâàÊú¨
    versions(type, id) {
        if (!id) return;
        return $http({
            method: "GET",
            url: `/api/wiki/post/versions`,
            headers: {Accept: "application/prs.helper.v2+json"},
            params: {type: type, source_id: id},
        });
    },

    // Ëé∑ÂèñÊåáÂÆöÊîªÁï•
    view(post_id) {
        if (!post_id) return null;
        return $http({
            method: "GET",
            url: `/api/wiki/post/${post_id}`,
            headers: {Accept: "application/prs.helper.v2+json"},
        });
    },

    // ÂàõÂª∫/Êõ¥Êñ∞ÊîªÁï•
    save(params) {
        return $http({
            method: "POST",
            url: `/api/wiki/post`,
            headers: {Accept: "application/prs.helper.v2+json"},
            data: qs.stringify({post: params}),
        });
    },

    // ÊàëÁöÑÁôæÁßëÊîªÁï•ÂàóË°®
    myList(params) {
        return $http({
            method: "GET",
            url: `/api/my/wiki/posts`,
            headers: {Accept: "application/prs.helper.v2+json"},
            params: params,
        });
    },

    // Âà†Èô§ÊàëÁöÑÁôæÁßëÊîªÁï•
    myRemove(post_id) {
        if (!post_id) return null;
        return $http({
            method: "PUT",
            url: `/api/my/wiki/post/${post_id}/remove`,
            headers: {Accept: "application/prs.helper.v2+json"},
        });
    },
}

// ÁôæÁßëËØÑËÆ∫
let WikiComment = {
    // ÁôæÁßëËØÑËÆ∫ÂàóË°®
    list(type, id) {
        if (!id) return;
        return $http({
            method: "GET",
            url: `/api/wiki/comments`,
            headers: {Accept: "application/prs.helper.v2+json"},
            params: {type: type, source_id: id},
        })
    },

    // ÂàõÂª∫ÁôæÁßëËØÑËÆ∫
    save(params) {
        return $http({
            method: "POST",
            url: `/api/wiki/comment`,
            headers: {Accept: "application/prs.helper.v2+json"},
            data: qs.stringify({comment: params}),
        })
    },

    // ÊàëÁöÑÁôæÁßëËØÑËÆ∫ÂàóË°®
    myList(params) {
        return $http({
            method: "GET",
            url: `/api/my/wiki/comments`,
            headers: {Accept: "application/prs.helper.v2+json"},
            params: params,
        });
    },

    // Âà†Èô§ÊàëÁöÑÁôæÁßëËØÑËÆ∫
    myRemove(comment_id) {
        if (!comment_id) return null;
        return $http({
            method: "PUT",
            url: `/api/my/wiki/comment/${comment_id}/remove`,
            headers: {Accept: "application/prs.helper.v2+json"},
        });
    }
}

// Èù¢ÂåÖÂ±ë
function getBread(channel){
    return $http.get('api/breadcrumb/' + channel,{
        headers: {Accept: "application/prs.helper.v2+json"},
    })
}

export {
    WikiPost,
    WikiComment,
    getBread
};
